export default "/*-- Navigation --*/\n.nav {\n  background: #eee;\n  backface-visibility: hidden;\n  -webkit-backface-visibility: hidden;\n  box-shadow: inset -8px 0 8px -6px rgba(0, 0, 0, 0.2);\n  display: none;\n  /* deal with FOUC */\n  height: 100%;\n  overflow-y: auto;\n  padding: 3%;\n  position: absolute;\n  top: 0;\n  transform: translate3d(-100%, 0, 0);\n  width: 270px;\n}\n:host-context(.nav-closed) .nav, :host-context(.nav-open) .nav {\n  display: block;\n  /* deal with FOUC */\n}\n.nav .active {\n  font-weight: bold;\n}\n.nav-list {\n  list-style: none;\n  margin-bottom: 0;\n  padding-left: 0;\n}\n.nav-list a {\n  display: block;\n  padding: 6px;\n}\n.nav-list a:hover, .nav-list a:active, .nav-list a:focus {\n  text-decoration: none;\n}\n/*-- Hamburger toggle --*/\n.toggle-offcanvas {\n  border-right: 1px solid rgba(255, 255, 255, 0.5);\n  display: inline-block;\n  height: 50px;\n  padding: 23.5px 13px;\n  position: relative;\n  text-align: center;\n  width: 50px;\n  z-index: 100;\n}\n.toggle-offcanvas span,\n.toggle-offcanvas span:before,\n.toggle-offcanvas span:after {\n  background: #fff;\n  border-radius: 1px;\n  content: \"\";\n  display: block;\n  height: 3px;\n  position: absolute;\n  transition: all 250ms ease-in-out;\n  width: 24px;\n}\n.toggle-offcanvas span:before {\n  top: -9px;\n}\n.toggle-offcanvas span:after {\n  bottom: -9px;\n}\n:host-context(.nav-open) .toggle-offcanvas span {\n  background: transparent;\n}\n:host-context(.nav-open) .toggle-offcanvas span:before, :host-context(.nav-open) .toggle-offcanvas span:after {\n  top: 0;\n}\n:host-context(.nav-open) .toggle-offcanvas span:before {\n  transform: rotate(45deg);\n}\n:host-context(.nav-open) .toggle-offcanvas span:after {\n  transform: rotate(-45deg);\n}\n/*-- Header and title --*/\n.header-page {\n  color: #fff;\n  height: 50px;\n  margin-bottom: 10px;\n  position: relative;\n}\n.header-page-siteTitle {\n  font-size: 30px;\n  line-height: 50px;\n  margin: 0;\n  padding: 0 0 0 60px;\n  position: absolute;\n  top: 0;\n  width: 100%;\n}\n.header-page a {\n  color: #fff;\n  text-decoration: none;\n}\n/*# sourceMappingURL=data:application/json;base64, */"